
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  font-family: 'Montserrat', sans-serif;
}
#app {
  background-image: url(/static/img/wet-bg.ed97010.jpg);
  background-size: cover;
  background-position: center;
  -webkit-transition: .4s;
  transition: .4s;
}
#app.hot {
  background-image: url(/static/img/hot-bg.bd802af.jpg);
}
#app.rain {
  background-image: url(/static/img/rainy-bg.f0e3224.jpg);
  background-position: bottom;
  background-size: cover;
}
#app.cloud {
  background-image: url(/static/img/wet-bg.ed97010.jpg);
}
main {
  min-height: 100vh;
  padding: 25px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.25)), to(rgba(0, 0, 0, 0.75)));
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.75));
}
.search-box {
  width: 100%;
  margin-bottom: 30px;
}
.search-box input {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 500;
}
.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;
  color: #313131;
  font-size: 20px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  outline: none;
  background: none;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 16px 0px 16px;
  -webkit-box-shadow: 0 0 8px rgba(0, 0, 0, 0.25);
          box-shadow: 0 0 8px rgba(0, 0, 0, 0.25);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.search-box .search-bar:focus {
  -webkit-box-shadow: 0 0 16px rgba(0, 0, 0, 0.25);
          box-shadow: 0 0 16px rgba(0, 0, 0, 0.25);
  background-color: rgba(255, 255, 255, 0.75);
  border-radius: 16px 0 16px 0;
}
.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 1px 3px rgba(0, 0, 0, 0.25);
  -webkit-transition-duration: 0.s;
          transition-duration: 0.s;
}
.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  text-align: center;
  font-style: italic;
}
.weather-box {
  text-align: center;
}
.weather-box .temp {
  display: inline-block;
  padding: 50px 55px;
  color: #fff;
  font-size: 102px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(0, 0, 0, 0.25);
  background-color: rgba(255, 255, 255, 0.25);
  border-radius: 16px;
  margin: 50px 0;
  -webkit-box-shadow: 3px 6px rgba(0, 0, 0, 0.25);
          box-shadow: 3px 6px rgba(0, 0, 0, 0.25);
}
.weather-box .weather {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  font-style: italic;
  text-shadow: 3px 6px rgba(0, 0, 0, 0.25);
}
.temp span {
  display: inline-block;
  border: 9px solid #fff;
  border-radius: 50%;
  -webkit-box-shadow: 3px 6px rgba(0, 0, 0, 0.25);
          box-shadow: 3px 6px rgba(0, 0, 0, 0.25);
  background: none;
  margin: 0 5px;
  vertical-align: top;
  width: 35px;
  height: 35px;
  margin-top: 15px;
}
.weather-box .weather-icon {
  margin-top: 30px;
  width: 100px;
  height: 100px;
}
